$sb-font-folder: "../fonts"; // relative to the final css file
@font-face {
    font-family: 'socialfont';
    src:url('#{$sb-font-folder}/socialfont.eot?mh1paq');
    src:url('#{$sb-font-folder}/socialfont.eot?#iefixmh1paq') format('embedded-opentype'),
    url('#{$sb-font-folder}/socialfont.woff?mh1paq') format('woff'),
    url('#{$sb-font-folder}/socialfont.ttf?mh1paq') format('truetype'),
    url('#{$sb-font-folder}/socialfont.svg?mh1paq#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class*=" icon-"],
[class^=icon-] {
    font-family: socialfont;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
}

@mixin icon($character,
$color) {
    &:before {
        content: $character;
    }
    &.color:hover {
        background-color: $color;
    }
    &.borderless:hover {
        color: $color;
    }
}

